import React, { useState, useCallback, useEffect, useRef, Fragment } from 'react';
import send from './images/send.svg'
import { useSpeechRecognition } from 'react-speech-kit';
import speak from './speak';
import { getDatabase, ref, onValue } from "firebase/database";
import axios from 'axios';
import ReactMarkdown from 'react-markdown'
import volumeOn from './images/volume-high-solid.svg'
import volumeOff from './images/volume-xmark-solid.svg'
import micOn from './images/microphone-solid.svg'
import micOff from './images/microphone-slash-solid.svg'
import { Blocks } from 'react-loader-spinner'

const getMessageTypeString = (msgType) => {
    switch(msgType) {
        case 0:
            return "System";
        case 1:
            return "Acharya"
        case 2: default:
            return "Me"
    }
}

function App() {
  const [value, setValue] = useState('');
  const [started, isStarted] = useState(false);
  const [loading, isLoading] = useState(false);
  const [volume, setVolume] = useState(false);
  const [mic, setMic] = useState(false);
  const [plan, setPlan] = useState(null);
  // const [resultHistory, setResultHistory] = useState([]);
  const [chatId, setChatId] = useState(null);
  const appRef = useRef(null);
  const messageRef = useRef(null)
  const { listen, listening, stop } = useSpeechRecognition({
    onResult: (result) => {
      setValue(result);
    },
  }, []);

  const [chats, setChats] = useState([]);
  const db = getDatabase();
  useEffect(() => {
    if (chatId === null) return;
    const chatsRef = ref(db, `chats/${chatId}/messages`);

    onValue(chatsRef, snapshot => {
      const chatsData = snapshot.val();
      const chatsArray = Object.values(chatsData);
      setChats(chatsArray);
    });
  }, [chatId, db]);

  useEffect(() => {
    if (mic) {
      listen()
    } else {
      stop()
    }
  }, [mic, listen, stop]);

  useEffect(() => {
    if (chatId === null) return;
    const planRef = ref(db, `chats/${chatId}/lesson_plan`);

    onValue(planRef, snapshot => {
      const planData = snapshot.val();
      setPlan(
      planData.split('\n').map(l => l.split(':')[0]).join('\n')
)
    });
  }, [plan, chatId, db]);

  useEffect(() => {
    // messageRef.current?.scrollIntoView({behavior: 'smooth'});
    if (!messageRef.current) return
    messageRef.current.scrollTop = messageRef.current.scrollHeight;
    if (chats.length > 0 && volume) {
      const lastChat = chats[chats.length - 1];
      if (lastChat.type === 2) return;
      speak(lastChat.content)
    }

    
  }, [chats, volume]);

  const consultKeyDown = async(e) => {
    if (!loading && (e.metaKey || e.ctrlKey)  && (e.key === 'Enter' || e.keyCode === 13)) {
      e.preventDefault();
      sendMessage()
    }
  }
  // useEffect(() => {
  //   const interval = setInterval(() => {
  //     if (value === '') return;
  //     if (resultHistory.length >= 5) {
  //       setResultHistory([...resultHistory.slice(1), value]);
  //     } else {
  //       setResultHistory([...resultHistory, value]);
  //     }
  //   }, 1000);
  //   return () => clearInterval(interval);
  // }, [value, resultHistory]);
  
  // useEffect(() => {
  //   if (resultHistory.length > 3) {
  //     if (new Set(resultHistory.slice(-3)).size === 1) {
  //       stop();
  //     }
  //   }
  // }, [resultHistory, stop]);

  let base = null;
  const startChat = useCallback(async (cont) => {
    const chat_id = localStorage.getItem('chat_id');
    isStarted(true);
    if (!chat_id || !cont) {
      await speak('Welcome to Achaarya "A.I", what would you like to learn?');
      const { data } = await axios.post('https://acharya-backend-auvrn2wyea-el.a.run.app/create_chat', {})
      localStorage.setItem('chat_id', data.chat_id);
      setChatId(data.chat_id)
    } else {
      await speak('Welcome back to Achaarya "A.I"');
      setChatId(chat_id)
    }
    // listen()
  }, []);

  const sendMessage = useCallback(async () => {
    isLoading(true)
    if (!chatId) return;
    await axios.post('https://acharya-backend-auvrn2wyea-el.a.run.app/get_reply', {
      "chat_id": chatId,
      "user_message": value.trim()
    })
    isLoading(false)
  }, [chatId, value]);

  if (!started) {
    base = (
      <div style={{display: 'flex', flexDirection: 'column', height: '100%', justifyContent: 'center', alignItems: 'center', backgroundColor: 'rgba(2,2,2,0.2)'}}>
        <h1 style={{ marginBottom: 0, color: 'white', fontSize: 64, fontFamily: 'monospace'}}>acharya.ai</h1>
        <p style={{ color: 'white' }}><i>Your AI Personal Tutor</i></p>
        <button style={{ width: 300, borderRadius: 8, fontSize: 20, padding: '18px 34px' }} onClick={() => startChat()}>Start</button>
        <button style={{ width: 300, borderRadius: 8, fontSize: 20, padding: '18px 34px', border: '3px solid #fff', background: 'transparent', color: 'white', marginTop: 20 }} onClick={() => startChat(true)}>Continue Last</button>
      </div>
    )
  } else {
    base = (
    <React.Fragment>
      <div className='messages' style={{marginTop: 40}} ref={messageRef}>
        {chats.slice(2).map((chat, index) => 
          (<div key={index} className={`message message-${chat.type}`}>
            <span>{ getMessageTypeString(chat.type) }</span>
            <ReactMarkdown>{ chat.content }</ReactMarkdown>
          </div>)
        )}
        {/* <div ref={messageRef} /> */}
      </div>
      <form onSubmit={(event) => sendMessage()}>
        {/* {listening ? <p style={{color: 'white' }}>Go ahead I'm listening</p> : <p> </p>} */}
        {/* <button onClick={() => listening ? stop() : listen()}>{ listening ? 'Stop' : 'Speak' }</button> */}
        <div style={{display: 'flex', justifyContent: 'center'}}>
          <textarea
            value={value}
            onChange={(event) => setValue(event.target.value)}
            onKeyDown={consultKeyDown}
          />
          <div id="send" onClick={() => !loading && sendMessage()}>
            {loading ? <Blocks
  visible={true}
  height="32"
  width="32"
  ariaLabel="blocks-loading"
  wrapperStyle={{}}
  wrapperClass="blocks-wrapper"
/>
: <img width={32} src={ send }/>}
          </div>

        </div>
      </form>
  </React.Fragment>
    )
  }

  return (
    <div className='app' ref={el => appRef.current = el}>
      <div id="top">
        <div style={{marginLeft: 40, marginRight: 'auto'}}>
        { plan && <Fragment>
            <h3 style={{color:'#eee', marginBottom: -20}}>Plan</h3>
            <pre style={{color:'#ccc', fontFamily: 'system-ui', fontSize: 16}}>{ plan }</pre>
            </Fragment>
          }
          <p style={{ color: '#ccc' }}>{ loading ? '' : ' ' }</p>
        </div>
        <div className="top-btn" id="vol" onClick={() => setVolume(!volume)}>
          {volume ? <img width={32} src={volumeOn} /> : <img width={32} src={volumeOff}/>}
        </div>
        <div className="top-btn" id="mic" onClick={() => setMic(!mic)}>
          {mic ? <img width={32} src={micOn} /> : <img width={32} src={micOff}/>}
        </div>
        <div>
      </div>
      </div>
      {/* <div align="center">
        <img align="center" id="acharya" src={anchor}/>
      </div> */}
      <div className='overlay'>
        { base }
      </div>
    </div>
  );
}
export default App;
