import axios from 'axios';

export default async function speak(text) {
  const { data, status } = await axios.post(
    'https://api.elevenlabs.io/v1/text-to-speech/VR6AewLTigWG4xSOukaG', {
    "text": text.trim(),
    "model_id": "eleven_monolingual_v1",
    "voice_settings": {
      "stability": 0,
      "similarity_boost": 0
    }
  }, { 
      headers: {
      'xi-api-key': 'c4de8b57ed61a82750607713365199ba',
      'Accept': 'audio/mpeg'
    },
    responseType: 'arraybuffer'
  })
  // debugger
  var oBlob = new Blob([data], { "type": "audio/mpeg" });
  var audioURL = window.URL.createObjectURL(oBlob);
  var audio = new Audio();
  audio.src = audioURL;
  // const a = document.createElement('a');
  //   a.style.display = 'none';
  //   a.href = audioURL;
  //   // the filename you want
  //   a.download = 'todo-1.mpg';
  //   document.body.appendChild(a);
  //   a.click();
  //   window.URL.revokeObjectURL(audioURL);
  //   alert('your file has downloaded!'); 
  return audio.play();
  // debugger
}